.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.friend{
  color: purple;
  background-color: white;
  border-color: blue;
  border-radius: 10px;
  border-style: solid;
  font-size: 12pt;
  padding: 5px;
  margin: 10px;
}

.friend-container{
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: flex-end;
}
